<template>
  <div class="header">
    <v-layout row wrap>
      <v-flex xs12 sm12 md12>        
        <img id="header-image" alt="This is me" src="../../assets/TriMeteora.jpg" @load="writeText()">
        <div class="headerName">
          <h1>{{name}}<span v-if="name !== 'Thiago'" class="blinker"></span></h1>
          <h1>{{lastName}}<span v-if="name === 'Thiago'" class="blinker"></span></h1>
          <v-layout row hidden-md-and-down v-if="lastName === 'Moreira'">
            <v-flex xs12 sm12 md12>        
              <ul class="subtitle">
                <li>Fullstack Developer</li>
                <li>JavaScript Enthusiast</li>
                <li>Computer Scientist</li>
              </ul>
            </v-flex>
          </v-layout>
        </div>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      name: '',
      lastName: '',
    };
  },
  methods: {
    writeText() {
      setTimeout(() => {
        this.typeWritter(['Thiago', 'Moreira'], ['name', 'lastName'], 50, 0);
      }, 1000);
    },
    typeWritter(toBeWritten, name, speed, beginAt) {
      const self = this;
      let counter = beginAt;
      if (counter < toBeWritten[0].length) {
        this[name[0]] += toBeWritten[0].split('')[counter];
        counter += 1;
        setTimeout(() => {
          self.typeWritter(toBeWritten, name, speed, counter);
        }, speed);
      } else {
        toBeWritten.shift();
        name.shift();
        if (toBeWritten.length > 0) {
          self.typeWritter(toBeWritten, name, speed, 0);
        }
      }
    },
  },
};
</script>

<style scoped>
.header {
  position: relative;
  clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 5vw), 0% 100%);
}
.header img {
  width: 100%;
}
.headerName {
  position: absolute;
  width: 40%;
  top: 35%;
  left: 55%;
  line-height: 8.2vw;
}
.headerName h1 {
  font-family: 'Computerfont';
  font-size: 10vw;
}
.headerName h1:first-child {
  margin-left: -20%;
}

.blinker::before {
  content: "|";
  animation-name: blinker;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(1,0,0,1);
  animation-duration: 1s;
}

@keyframes blinker { 
  from { opacity: 1.0; } to { opacity: 0.0; }
}

@keyframes fadeIn { 
  from { opacity: 0.0; } to { opacity: 1.0; }
}

.subtitle {
  line-height: normal;
  text-align: right;
  list-style-type: none;
  margin-right: 20%;
  font-size: 1.3vw;
  opacity: 1;

  animation-name: fadeIn;
  animation-iteration-count: unset;
  animation-timing-function: cubic-bezier(1,1,0,1);
  animation-duration: 2s;
}
</style>



