<template>
  <v-card height="100%">
    <v-card-text>
      <h1>Skills</h1>     
      <DataRange title="JavaScript" :levels="levels" myLevel="Advanced"/>
      <DataRange title="VueJS" :levels="levels" myLevel="Advanced"/>
      <DataRange title="Node.js" :levels="levels" myLevel="Advanced"/>
      <DataRange title="NoSQL" :levels="levels" myLevel="Advanced"/>
      <DataRange title="React" :levels="levels" myLevel="Gets stuff done"/>
      <DataRange title="React Native" :levels="levels" myLevel="Gets stuff done"/>
    </v-card-text>
  </v-card>
</template>

<script>
import DataRange from '@/components/DataRange/DataRange'
export default {
  name: 'Skills',
  components: {
    DataRange,
  },
  data() {
    return {
      levels: ['Beginner', 'Gets stuff done', 'Advanced'],
    };
  },
  methods: {
    skill(val) {
      return this.levels[val];
    },
  },
};
</script>

<style scoped>
  h1 {
    text-align: center;
    font-family: 'Computerfont';
    font-size: 5rem;
  }
</style>



