<template>
 <v-container fluid wrap>    
   <Header/>
   <Readme/>
   <v-layout row wrap>
     <v-flex xs12>
       <Languages/>
     </v-flex>
   </v-layout>   
   <v-layout row wrap>
     <v-flex xs12 sm12 md12 lg4 pa-4 pb-5>       
       <Skills/>
     </v-flex>
     <v-flex xs12 sm12 md12 lg4 pa-4 pb-5>
       <Career/>
     </v-flex>
     <v-flex xs12 sm12 md12 lg4 pa-4 pb-5>
       <Education/>
     </v-flex>
   </v-layout>
   <v-layout row wrap>
     <v-flex xs12>
       <Portfolio/>
     </v-flex>
   </v-layout>   
 </v-container>
</template>

<script>
import Header from './Header/Header';
import Readme from './Readme/Readme';
import Languages from './Languages/Languages';
import Skills from './Skills/Skills';
import Career from './Career/Career';
import Education from './Education/Education';
import Portfolio from './Portfolio/Portfolio';

export default {
  name: 'Main',
  components: {
    Header,
    Readme,
    Languages,
    Skills,
    Career,
    Education,
    Portfolio,
  },
  data() {
    return {
      Header: '',
    };
  },
};
</script>


<style>
  .container {
    padding: 0px !important;
    margin: 0px;
  }
  .padding-sides {
    padding: 0px 15px;
  }
</style>




