<template>
  <v-card height="100%">
    <v-card-text>
      <v-layout row wrap>
        <v-flex>
          <h1>Education</h1>                 
        </v-flex>
      </v-layout>
      <div v-for="(diplom, index) in diploms" :key="index">
        <v-layout row wrap>
          <v-flex>
            <p class="title">{{diplom.title}}</p>
          </v-flex>
        </v-layout>
        <v-layout row wrap>
          <v-flex>
            <p class="description">
              {{diplom.description}}
            </p>
          </v-flex>
        </v-layout>
      </div>
    </v-card-text>
  </v-card>
</template>
<script>
export default {
  name: 'Education',
   data() {
    return {
      diploms: [
        {
          title: 'Federal Center for Technological Education of Rio de Janeiro (CEFET-RJ)',
          description: 'Tutor of the Computer Algorithms class for one year. In this work I was an assistant from the Professor of this class, helping him with the students questions out of the class. For this service I became a sponsorship from the univiersity.',
        },
        {
          title: 'Freie Universitaet Berlin',
          description: 'Scholarship holder from the Science without Borders program. This program was a parnership from the Brazilian Government with the Deutsche Akademischer Austaschdients (DAAD). I had the honour of living in Germany and study there for one and a half year.',
        },
      ]
    };
  },
}
</script>
<style scoped>
  h1 {
    text-align: center;
    font-family: 'Computerfont';
    font-size: 5rem;
  }
  .title {
    text-align: left;
    color: #2c3e50;
    font-family: 'sans-serif';
    border-bottom: solid #2c3e50;
  }
  .description {
    text-align: justify;
    color: #2c3e50;
    font-size: 1.2rem;
  }
</style>



