<template>
  <v-card height="100%">
    <v-card-text>
      <v-layout row wrap>
        <v-flex>
          <h1>Career</h1>                 
        </v-flex>
      </v-layout>
      <div v-for="(job, index) in jobs" :key="index">
        <v-layout row wrap>
          <v-flex>
            <p class="title">{{job.title}}</p>
          </v-flex>
        </v-layout>
        <v-layout row wrap>
          <v-flex>
            <p class="description">
              {{job.description}}
            </p>
          </v-flex>
        </v-layout>
      </div>
    </v-card-text>
  </v-card>
</template>
<script>
export default {
  name: 'Career',
  data() {
    return {
      jobs: [
        {
          title: 'IBM - Fullstack Developer',
          description: 'Fullstack developer, responsible for many key applications in the organization. I was also responsible for renewing our technology stack from Angular v1 to VueJS, adopted Webpack and microservices strategy.',
        },
        {
          title: 'IBM - Intern',
          description: 'Fullstack developer using Angular v1 and Node.js. I\'ve implemented the first Microservice of our team, responsible for serving files (upload and download) from server. I also took part of the development process of a software (internal use) with more then two thousend users.',
        },
        {
          title: 'Brazilian Air Force - Student Researcher',
          description: 'Frontend developer working in a partnership between the University of Rio de Janeiro and the Brazilian Air Force. I was responsible for creating rich interfaces for a management application called NEXO.',
        },
      ]
    };
  },
};
</script>
<style scoped>
  h1 {
    text-align: center;
    font-family: 'Computerfont';
    font-size: 5rem;
  }
  .title {
    text-align: left;
    color: #2c3e50;
    font-family: 'sans-serif';
    border-bottom: solid #2c3e50;
  }
  .description {
    text-align: justify;
    color: #2c3e50;
    font-size: 1.2rem;
  }
</style>



