<template>
  <div class="portfolioWrapper" ref="portfolioWrapper">
    <v-layout row wrap>
      <v-flex xs12>
        <h1>Portfolio</h1>     
      </v-flex>
    </v-layout>
    <v-layout row wrap class="pa-3">
      <v-flex xs12 md6>
        <v_video :src="toPlay"/>
      </v-flex>
      <v-flex xs12 md4 class="pa-3">
      <p class="title pb-1">
        vue-video        
      </p>
      <p class="description">
        This project is my first <b>Vue.js</b> component published to NPM in order to collaborate with the opensource community. Which I consider a big step to every developer.
      </p>
      <p class="description">
        It's always easy to write some code at home or even in a company where just your collegues are able to judge the quality of your code. To open your source code to everyone and accept help to improve what was just yours, is a nice way of creating better things.
      </p>
      <p class="description">
        This component is a video player styled from scratch having the <i>video tag</i> from HTML5 as basis.
      </p>
      <span class="description portButton">
        <a href="https://github.com/ThiagoDella/v-video" target="_blank">
          <v-icon medium>fa-github</v-icon>
          See on GitHub
        </a>
      </span>
      </v-flex>      
    </v-layout>
  </div>
</template>
<script>
import v_video from '@thiago.dlm/vue-video';
import toPlay from '@/assets/lunar_module.mp4';
console.log(toPlay);

export default {
  name: 'Portfolio',
  components: {
    v_video,
  },
  data() {
    return {
      toPlay: toPlay
    }
  },
}
</script>
<style scoped>
  .pathed {
    clip-path: polygon(0% calc(0% + 5vw), 100% 0%, 100% 100%, 0% 100%);  
  }
  .portfolioWrapper {
    background-color: #2c3e50;
    padding-top: 5vw;
    color: #ededed;
    /* height: 50vh; */
    text-align: center;
  }
  h1 {
    font-family: 'Computerfont';
    font-size: 5rem;
  }
  .description {
    color: #ccc;
    font-size: 1.2rem;
  }
  .title {
    text-align: left;
    color: #ccc;
    font-family: 'sans-serif';
    border-bottom: solid #ccc;
  }
  .description {
    text-align: justify;
    color: #ccc;
    font-size: 1.2rem;
  }
  .portButton {
    float: left;
    border: solid 1px;
    display: inline-block;
    padding: 5px;
    border-radius: 5px;
  }
  .portButton:hover {
    background-color: #ccc;
    color: #2c3e50 !important;
    float: left;
    border: solid 1px;
    display: inline-block;
    padding: 5px;
    border-radius: 5px;
  }
  a, a:link, a:visited, a:active {
    color: inherit;
    text-decoration: none;
  }
  a:hover {
    color: #2c3e50
  }
</style>



