<template>
  <div class="languagesWrapper mt-5 mb-5">
    <h1>Languages</h1>
    <p class="description">
      I do speak those languages.      
    </p>
    <p class="flags">
      <span class="flag-icon flag-icon-br"></span>
      <span class="flag-icon flag-icon-de"></span>
      <span class="flag-icon flag-icon-gb"></span>
    </p>
  </div>
</template>
<script>
export default {
  name: 'Languages',
}
</script>
<style scoped>
  .languagesWrapper{
    text-align: center;
  }
  h1 {
    text-align: center;
    font-family: 'Computerfont';
    font-size: 5rem;
  }
  .description {
    color: #2c3e50;
    font-size: 1.2rem;
  }
  .flags {
    font-size: 2rem;
    opacity: 0.8;
    cursor: pointer;
  }
</style>



