<template>
  <div>
    <v-layout row wrap>
      <v-flex class="title pb-2">
        {{title}}
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex v-for="(level, index) in levels" :key="index" :class="getLevel(index)">
      </v-flex>
    </v-layout>  
    <v-layout row wrap>
      <v-flex v-for="(level, index) in levels" :key="index" class="levels mt-2 mb-4">
        {{level}}
      </v-flex>
    </v-layout>      
  </div>
</template>
<script>
export default {
  name: 'DataRange',
  props: ['title', 'levels', 'myLevel'],
  methods: {
    getLevel: function(index) {
      if (index <= this.levels.indexOf(this.myLevel)) {
        return 'grade';
      }
      return 'blankGrade';
    },
  },
};
</script>
<style scoped>
  .title {
    text-align: left;
    font-family: 'sans-serif';
  }
  .grade {
    height: 10px;
    background-color: #2c3e50;
    border-right: solid 1px #ededed;
  }
  .grade:first-child{
    border-left: solid 1px #ededed;
  }
  .blankGrade {
    height: 10px;
    background-color: #ccc;
    border-right: solid 1px #ededed;
  }
  .levels {
    text-align: center;
    color: #2c3e50;
  }
</style>



